@import url(https://fonts.googleapis.com/css2?family=Playball&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&family=Playball&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&family=Fredericka+the+Great&family=Playball&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* @import url("https://fonts.googleapis.com/css2?family=Playball&display=swap"); */
/* @import url('https://fonts.googleapis.com/css2?family=Playball&display=swap'); */

.MAIN {
  max-width: 360px;
  margin: auto;
  padding: 3px;
  background-image: url(/MBW21/static/media/BI.81cb7cf4.gif);
}

.photo-box {
  width: 100%;
  height: auto;
  margin: 2% auto;
}

.photo-box img {
  width: 100%;
  display: block;
}

.MESS {
  /* font-family: "Cabin Sketch", cursive; */
  font-family: "Fredericka the Great", cursive;
  color: brown;
  font-size: x-large !important ;
}

.TAG {
  font-family: "Playball", cursive;
}

h3 {
  margin: 0px;
}

.jumbotron {
  padding-right: 5px !important;
  padding-left: 5px !important;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
  margin-bottom: 0px !important;
  background-color: rgba(231, 231, 231, 1);
}

.ANI {
  animation: phosli 3.4s infinite ease-in-out;
}

@keyframes phosli {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

