.MAIN {
  max-width: 360px;
  margin: auto;
  padding: 3px;
  background-image: url("./assets/imgs/BI.gif");
}

.photo-box {
  width: 100%;
  height: auto;
  margin: 2% auto;
}

.photo-box img {
  width: 100%;
  display: block;
}

.MESS {
  /* font-family: "Cabin Sketch", cursive; */
  font-family: "Fredericka the Great", cursive;
  color: brown;
  font-size: x-large !important ;
}

.TAG {
  font-family: "Playball", cursive;
}

h3 {
  margin: 0px;
}

.jumbotron {
  padding-right: 5px !important;
  padding-left: 5px !important;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
  margin-bottom: 0px !important;
  background-color: rgba(231, 231, 231, 1);
}

.ANI {
  animation: phosli 3.4s infinite ease-in-out;
}

@keyframes phosli {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes phosli {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes phosli {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes phosli {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}
